import React from "react"

const MarkerSize = 20;

export default class SimplePlayerMarker extends React.Component {
    constructor(props) {
        super(props);

        this.nonHoveredStyles = {
            position: 'relative',
            width: MarkerSize + 'px',
            height: MarkerSize + 'px',
            top: -MarkerSize / 2 + 'px',
            left: -MarkerSize / 2 + 'px',
            backgroundColor: 'red',
            borderRadius: MarkerSize + 'px',
            border: 'none'
        }
    
        this.hoveredStyle = {
            ...this.nonHoveredStyles,
            color: 'yellow'
        }
    }

    render() {
        const style = this.props.hover ? this.hoveredStyle : this.nonHoveredStyles;

        return (
            <div style={style}>
                {this.props.hover && <span style={{position: 'absolute'}}>{this.props.login}</span>}
            </div>
        );
    }
}


