import React from "react"
import request from "./request"
import GameMap from "./GameMap"
import SimpleMarker from "./SimplePlayerMarker"

export default class WatcherInterface extends React.Component {

    constructor(props) {
        super(props);

        let defaultParams = {
            center: {
                lat: 35,
                lng: 0
            },
            zoom: 4,
            playersOnline: [],
            hoverID: null
        }

        this.state = defaultParams;

        this.checkPlayersOnline = this.checkPlayersOnline.bind(this);
        this.onChildHoverEnter = this.onChildHoverEnter.bind(this);
        this.onChildHoverLeave = this.onChildHoverLeave.bind(this);
        this.onBoundsChange = this.onBoundsChange.bind(this);

        setInterval(this.checkPlayersOnline, 5000);
    }

    onChildHoverEnter(key) {
        this.setState({ hoverID: key });
    }

    onChildHoverLeave(key) {
        this.setState({ hoverID: null });
    }

    onBoundsChange = (center, zoom) => {
        this.setState({ center, zoom });
        console.log(center);
    }

    checkPlayersOnline() {
        request({
            method: 'GET',
            url: '/user/watcher/getOnlinePlayers'
        })
            .then((responce) => {
                let { players: playersID } = responce.data;

                if (playersID.length == 0) {
                    this.setState({ playersOnline: [] });
                    return;
                }

                let players = [];

                for (let i = 0; i < playersID.length; i++) {

                    let num = i;

                    request({
                        method: 'GET',
                        url: '/user/userInfo',
                        params: {
                            id: playersID[num],
                            fields: [
                                'login',
                                'position'
                            ]
                        }
                    })
                        .then((responce) => {
                            players.push(responce.data);
                            if (num == playersID.length - 1) this.setState({ playersOnline: players });
                        })
                }
            })
    }

    componentDidMount() {
        getCurrentPosition()
            .then((position) => {
                let { latitude: lan, longitude: lng } = position.coords;
                this.setState({ center: { lan, lng }, zoom: 18 });
            })
            .catch((error) => console.log(error));
    }

    render() {

        let players = this.state.playersOnline.map((player) => {
            console.log("player");
            return <SimpleMarker
                key={player.id}
                login={player.login}
                hover={this.state.hoverID === player.id}
                lan={player.position.latitude}
                lng={player.position.longitude}

            />
        })

        return (
            <div>
                <GameMap
                    center={this.state.center}
                    zoom={this.state.zoom}
                    google={window.google}
                    onChildMouseEnter={this.onChildHoverEnter}
                    onChildMouseLeave={this.onChildHoverLeave}
                    onBoundsChange={this.onBoundsChange}
                    childs={players}
                />
            </div>
        );
    }

}

function getCurrentPosition() {
    return new Promise((resolve, reject) => {
        if (navigator.geolocation)
            navigator.geolocation.getCurrentPosition((position) => resolve(position))
        else reject()
    })
}

